:root {
  --blue-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  margin: 0;
  display: flex;
}

html {
  font-family: Roboto, sans-serif;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  width: 1024px;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

@media (width >= 1200px) {
  .header {
    width: 1200px;
  }
}

.logo {
  display: block;
}

.nav {
  align-items: center;
  display: flex;
}

.nav_list {
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  align-items: center;
  margin: 0 10px;
  list-style-type: none;
  display: flex;
}

.nav_link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__button {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__button:hover, .nav__button.is-active, .nav_link:hover {
  color: var(--blue-color);
}

.nav__button.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -.1px;
  left: 0;
  right: 0;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav_item:first-child {
  margin-left: 0;
}
